import React from 'react'; import styled from 'styled-components'; 
// -------------------------------------------------------
import '~CSS/SlickSlider.scss'; import ImageGallery from 'react-image-gallery';
const Music = () => (
    <Wrapper>
        <Text> 
            <Header>Music</Header>
        </Text>
        <ImageGallery styles={{height: '1000px'}} items={images}></ImageGallery>
    </Wrapper>
)
// Styles flex item -----------------------------------------------------
const Wrapper = styled.div `height: 100%; width: 100%; display: flex; flex-direction: column; background: rgba(0,0,0,.8);`
// -- About music text
const Text    = styled.div `width: 100%; display: flex; justify-content: center; background: rgba(26,0,13,.9);`
    const Header  = styled.h2  ` padding: 4px 0 8px 0; text-align: center; font-size: 180%; color: #fff; `
    // ---------------Images
const images = [
    {
        original: 'https://i.postimg.cc/P5FQSqcD/first.jpg',
        thumbnail: 'https://i.postimg.cc/P5FQSqcD/first.jpg',
    },
    {
        original: 'https://i.postimg.cc/W30mpw4m/two.jpg',
        thumbnail: 'https://i.postimg.cc/W30mpw4m/two.jpg',
    },
    {
        original: 'https://i.postimg.cc/bv7HjBqN/three.jpg',
        thumbnail: 'https://i.postimg.cc/bv7HjBqN/three.jpg',
    },
    {
        original: 'https://i.postimg.cc/L5RtJgqc/four.jpg',
        thumbnail: 'https://i.postimg.cc/L5RtJgqc/four.jpg',
    },
    {
        original: 'https://i.postimg.cc/jjwQmb0F/five.jpg',
        thumbnail: 'https://i.postimg.cc/jjwQmb0F/five.jpg',
    }
    ];
    export default Music